import React from 'react'
import '../Styles/App.css'
import TodoHead from '../Components/todohead'
import TodoInput from '../Components/todoInput'

class ToDoList extends React.Component {
    constructor() {
        super();
        this.state = {
            notes: [],
        }
    }

    //Onclicking Submit Button
    submitNote = (e,todo,time) => {
        let newNote = {
            todo: todo,
            estTime: time,
            startTime: 0,
            endTime: 0
        }
        this.setState({
            notes : [...this.state.notes, newNote]
        })
    }

    //Getting current time in milliseconds
    getTimeMilli = () => {
        var d = new Date();
        return d.getTime();
    }

    //Getting Current time in format HH:MM:SS
    getFormatTime = (timiInMilliseconds) => {
        return new Date(timiInMilliseconds).toISOString().substr(11,8)
    }

    //Onclick Of starting a ToDO
    start = (e) => {
        let time = this.getTimeMilli();
        this.setState({
            notes: this.state.notes.map((el, i) => (i == e.target.name ? Object.assign({}, el, { startTime: time }) : el))
        })
    }

    //Onclick of ending the ToDO
    end = (e) => {
        let time = this.getTimeMilli();
        if (this.state.notes[e.target.name].startTime != 0) {
            this.setState({
                notes: this.state.notes.map((el, i) => (i == e.target.name ? Object.assign({}, el, { endTime: time }) : el))
            })
        }
        else {
            alert("Please Start the task first")
        }
    }


    render() {
        const todo = this.state.notes.map((element, index) => {
            return (
                <tr key={index}>
                    
                    <td style={{ textAlign: "center" }}>{element.todo}</td>

                    <td style={{ textAlign: "center" }}>{element.estTime}</td>

                    {
                    this.state.notes[index].startTime === 0 ? 
                    <td style={{ textAlign: "center" }}>
                         <div style={{ width: "100%" }}>
                            <button name={index} className="btn btn-primary btn-sm" type="button" variant="contained" color="primary" onClick={(e) => this.start(e)}>Start</button>
                        </div>
                    </td> 
                    : 
                    <td style={{ textAlign: "center" }}>{this.getFormatTime(this.state.notes[index].startTime)}</td>
                    }

                    {
                    this.state.notes[index].endTime === 0 ? 
                    <td style={{ textAlign: "center" }}>
                        <div style={{ width: "100%" }}> 
                            <button name={index} className="btn btn-primary btn-sm" type="button" variant="contained" color="primary" onClick={(e) => this.end(e)}>End</button> 
                        </div>
                    </td> 
                    : 
                    <td style={{ textAlign: "center" }}>{this.getFormatTime(this.state.notes[index].endTime)}</td>
                    }

                    {
                    this.state.notes[index].endTime !== 0 ? 
                    <td style={{ textAlign: "center" }}>{this.getFormatTime(this.state.notes[index].endTime - this.state.notes[index].startTime)}</td> 
                    : 
                    <td style={{ textAlign: "center" }}>--:--:--</td>
                    }

                </tr>
            )
        })

        return (
            <div className="container">
                <div className="scores-block1 clearfix">
                    <div className="bg-white shadow-sm Assessment_Results">
                        <TodoInput submitNote = {(e,todo,time)=>this.submitNote(e,todo,time)}/>
                        {
                        this.state.notes.length !== 0 && 
                        <table className="table" style={{ marginTop: "2rem" }}>
                           <TodoHead />
                            <tbody >
                                {todo}
                            </tbody>
                        </table>
                        }
                    </div>
                </div>
            </div>
        );
    }
}

export default ToDoList;